import React from 'react';
import L from 'leaflet';
import { Map, Marker, TileLayer, LayersControl, LayerGroup } from 'react-leaflet';

import 'leaflet/dist/leaflet.css'; // Solve this styles import for Leaflet another way in future!

// Components import
import PopupGeneral from '../popup/PopupGeneral';
import PanController from './PanController';
import PanUpButton from '../buttons/PanUpButton';
import PanRightButton from '../buttons/PanRightButton';
import PanDownButton from '../buttons/PanDownButton';
import PanLeftButton from '../buttons/PanLeftButton';
import LogoButton from '../buttons/LogoButton';
import customIcon from './map-markers/customIcon';

// Styles import
import './PanController.css';
import './LayersController.css';
import './MainMap.css';
import './ZoomController.css';
// import './leaflet.css';

const position = [49.00, 32.00];
const zoomLevel = 8;
const { Overlay } = LayersControl;

class MainMap extends React.Component {
  constructor(props) {
    super(props);
    this.state = { currentZoomLevel: zoomLevel };
    this.state = { currentPosition: position };
    this.handleUpPanClick = this.handleUpPanClick.bind(this);
    this.handleRightPanClick = this.handleRightPanClick.bind(this);
    this.handleDownPanClick = this.handleDownPanClick.bind(this);
    this.handleLeftPanClick = this.handleLeftPanClick.bind(this);
  }

  componentDidMount() {
    const leafletMap = this.leafletMap.leafletElement;
    leafletMap.on('zoomed', () => {
      const updatedZoomLevel = leafletMap.getZoom();
      this.handleZoomLevelChange(updatedZoomLevel);
    });
    leafletMap.on('panned', () => {
      const updatedPosition = leafletMap.panBy();
      this.handlePositionChange(updatedPosition);
    });
  }

  handleZoomLevelChange(newZoomLevel) {
    this.setState({ currentZoomLevel: newZoomLevel });
  }

  handlePositionChange(newPosition) {
    this.setState({ currentPosition: newPosition });
  }

  handleUpPanClick() {
    const leafletMap = this.leafletMap.leafletElement;
    leafletMap.panBy([0, -100]);
  }

  handleRightPanClick() {
    const leafletMap = this.leafletMap.leafletElement;
    leafletMap.panBy([100, 0]);
  }

  handleDownPanClick() {
    const leafletMap = this.leafletMap.leafletElement;
    leafletMap.panBy([0, 100]);
  }

  handleLeftPanClick() {
    const leafletMap = this.leafletMap.leafletElement;
    leafletMap.panBy([-100, 0]);
  }

  render() {
    return (
      <Map
        ref={(m) => { this.leafletMap = m; }}
        center={position}
        zoom={zoomLevel}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        />
        <LayersControl>
          <Overlay name="Custom marker">
            <Marker
              position={position}
              icon={customIcon}
            >
              <PopupGeneral />
            </Marker>
          </Overlay>
        </LayersControl>
        <LogoButton />
        <div className="pan-controller">
          <PanUpButton onClick={this.handleUpPanClick} />
          <PanRightButton onClick={this.handleRightPanClick} />
          <PanDownButton onClick={this.handleDownPanClick} />
          <PanLeftButton onClick={this.handleLeftPanClick} />
        </div>
      </Map>
    );
  }
}

